@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,700;1,500&display=swap);
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}
a {
    text-decoration: none;
}
.container {
    margin: 0 200px;
}
body {
    background-color: #19303f;
}
.h1 {
    color: #CD5A69;
    font-weight: normal;
}
.title {
    color: #fff;
    font-weight: bold;
}

.first {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 72vh;
}

.title-container h1 {
    font-size: 55px;
}
.title-container h5 {
    font-size: 30px;
    font-weight: normal;
    padding-top: 50px;
    margin-bottom: 50px;
    color: #fff;
}
.fullstack {
    color: #CD5A69;
}

.info {
    border: none;
    padding: 15px 30px;
    text-decoration: none;
    color: #fff;
    background-color: #CD5A69;
    border-radius: 50px;
    font-size: 25px;
    font-weight: 500;
    transition: all 0.5s ease;
}
.info:hover {
    background-color: #fff;
    color: #CD5A69;
    font-weight: bold;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.first img {
    margin-top: 100px;
    width: 700px;
    height: 700px;
}

@media screen and (max-width: 1440px) {
    .container {
        margin: 100px 50px;
    }
}

@media screen and (max-width: 1200px) {
    .container {
        margin: 0 30px;
        height: 80vh!important;
    }
    .first h1{
        font-size: 32px;
    }
    .first h5 {
        font-size: 24px;
    }
    .first img {
        width: 500px;
        height: 500px;
    }
    .info {
        font-size: 15px;
        padding: 10px 24px;
    }
}


@media screen and (max-width: 900px) {
    .container {
        height: 90vh!important;

    }
    .first {
        display: flex;
        flex-wrap: wrap;
        height: 80vh;
        justify-content: center;
        margin-top: 20px;
    }
}

@media screen and (max-width: 750px) {
    .first {
        height: 70vh;
    }
    .first img {
        width: 400px;
        height: 400px;
    }
    .info {
        font-size: 16px;
        padding: 7px 20px;
    }
}
@media screen and (max-width: 425px) {
    .container {
        height:120vh!important;

    }
    .first {
        height: 50vh;
    }
    .info {
        font-size: 16px;
        padding: 7px 20px;
    }
    .box-img {
        width: 300px!important;
        height: 300px!important;
    }
}
@media screen and (max-width:319px){

    .box-img {
        width: 300px!important;
        height: 300px!important;
    }
}

